			},{
				title: '我的',
				icon: 'people',
				choosed: false,
				url: '/pages/me/me'
			}],
			CustomBar: this.CustomBar,
			lessons: [
				{
					tid: 't123',
					title: '高三数学冲刺课',
					image: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big10006.jpg',
					city: '徐汇',
					price: '899',
					times: 4,
					createDate: '2019-11-27 09:22:00'
				},
				{
					tid: 't234',
					title: '高二化学试听课',
					image: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big10006.jpg',
					city: '静安',
					price: '599',
					times: 3,
					createDate: '2019-11-28 08:22:00'
				},
				{
					tid: 't234',
					title: '高三英语冲刺课',
					image: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big10006.jpg',
					city: '静安',
					price: '599',
					times: 3,
					createDate: '2019-11-28 08:22:00'
				},
			],
			showLocation: false,
		}
	},
	onLoad() {
		// uni.showLoading({title: '加载中...',mask: true});
	},
	onShow(){
		
	},
	onReady() {
		// uni.hideLoading()
		this.getLocation()
	},
	methods: {
		jumpTo(e, url){
			uni.navigateTo({
				url: url
			})
		},
		switchTo(e, url) {
			uni.redirectTo({
				url: url
			})
		},
		toLessonDetail(e, tid){
			uni.navigateTo({
				url: '/pages/lesson_detail/lesson_detail?tid='+tid
			})
		},
		showModal(e) {
			this.showLocation = true
		},
		hideModal(e) {
			this.showLocation = false
		},
		getLocation(e){
			var that = this
			uni.getLocation({
				success(res){
					let distance= that.getDistance(res.latitude, res.longitude, 31.18826, 121.43687)
					uni.showToast({
						icon: 'none',
						title: '你离环贸' + distance + '米'
					})
				},
				fail(res){console.log(res)},
				complete(res){that.showLocation = false}
			})
		},
		getDistance(x1, y1, x2, y2){
			let R = 6371393; 
			let C = this.cos(y1)*this.cos(y2)*this.cos(x1-x2) + this.sin(y1)*this.sin(y2);
		    let Distance = R*this.arccos(C);
		    return Distance;
		},
		arccos(i){
		    return Math.acos(i);
		},
		cos(i){
		    return Math.cos(i/180*Math.PI);
		},
		sin(i){
		    return Math.sin(i/180*Math.PI);
		},

	}
}
